<template>
  <div class="page page-roadshow-list">
    <div class="list-options">
      <!--  :labelCol="{ span: 10 }" :wrapperCol="{ span: 10 }"  -->
      <div class="flex items-center">
        <div class="flex items-center">
          <span>关键字：</span>
          <a-input size="small" v-model="params.keyword" style="width: 200px" placeholder="资源名称" />
        </div>
        <a-button style="margin-left: 50px" type="primary" icon="search" size="small" :loading="loading" @click="handleSearch">查询</a-button>
      </div>
    </div>
    <a-divider></a-divider>
    <a-space class="v-mb-md">
      <a-button type="primary" size="small" style="min-width: 60px" @click="showForm">新增</a-button>
      <a-button type="primary" size="small" style="min-width: 60px" @click="handleDel">删除</a-button>
    </a-space>
    <div class="list-container">
      <a-table
        :columns="columns"
        :dataSource="list"
        :pagination="{
          showQuickJumper: true,
          showSizeChanger: true,
          hideOnSinglePage: false,
          pageSizeOptions: ['10', '20', '30', '50'],
          total: pagination.totalRow,
          pageSize: pagination.pageSize,
          current: pagination.pageNumber,
          onChange: onPageChange,
          onShowSizeChange: onPageChange,
        }"
        :rowSelection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"
        :scroll="{ x: '100%' }"
        :loading="loading"
        tableLayout="auto"
        class="text-no-wrap"
        rowKey="id"
        size="small"
      >
        <template slot="action" slot-scope="text, record">
          <!-- <a-popconfirm @confirm="updateDoc(record)" cancelText="取消" okText="确认" placement="top">
            <template slot="title">
              <div v-html="record.status === 0 ? '确认冻结 ?' : '确认解冻 ?'"></div>
            </template>
            <a-icon slot="icon" style="color: red" type="info-circle" />
            <a-button class="text-indigo" :icon="record.status === 0 ? 'eye' : 'eye-invisible'" size="small" :title="record.status === 0 ? '发布' : '取消发布'" type="link"></a-button>
          </a-popconfirm> -->
          <a-button class="text-red" icon="audit" size="small" title="授权" type="link"></a-button>
          <a-button class="text-red" icon="edit" size="small" title="编辑" type="link"></a-button>
          <a-popconfirm placement="top" okText="确认" cancelText="取消" @confirm="onDelete(record)">
            <template slot="title">
              <div>确认删除 ?</div>
            </template>
            <a-icon slot="icon" type="info-circle" style="color: red" />
            <a-button class="text-red" icon="close" size="small" title="删除" type="link"></a-button>
          </a-popconfirm>
        </template>
      </a-table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      loading: false,
      list: [],
      selectedRowKeys: [],
      selectedRows: [],
      pagination: {
        totalPage: 1,
        pageNumber: 1,
        pageSize: 10,
        totalRow: 0,
      },
      params: {
        keyword: '',
        status: undefined,
      },
      columns: [
        {
          title: '序号',
          dataIndex: 'id',
          width: 50,
          align: 'center',
          customRender: (text, record, index) => index + 1,
        },
        {
          title: '编号',
          dataIndex: 'code',
        },
        {
          title: '资源名称',
          dataIndex: 'name',
          align: 'left',
        },
        {
          title: '关键字',
          dataIndex: 'keyword',
          // width: 100,
        },
        {
          title: '资源完整度',
          dataIndex: 'progress',
          // width: 100,
        },
        {
          title: '开发者',
          dataIndex: 'developor',
          align: 'center',
          width: 122,
        },
        {
          title: '所属单位',
          dataIndex: 'department',
          // width: 100,
        },
        {
          title: '审核状态',
          dataIndex: 'status',
          // width: 100,
        },
        {
          title: '上架日期',
          dataIndex: 'publishTime',
          align: 'center',
          width: 122,
        },
        {
          title: '引用次数',
          dataIndex: 'total',
          align: 'center',
          width: 60,
        },
        {
          title: '操作',
          dataIndex: 'action',
          width: 100,
          fixed: 'right',
          align: 'center',
          scopedSlots: { customRender: 'action' },
        },
      ],
    }
  },
  computed: {
    user: (vm) => vm.$store.state.user.user,
  },
  methods: {
    handleSearch(e) {
      this.load(1, 10)
    },
    onStatusChange() {},
    load(pageNumber = 1, pageSize = 10) {
      this.loading = true
      setTimeout(() => {
        let list = []
        for (let index = 0; index < pageSize; index++) {
          let idx = (pageNumber - 1) * pageSize + index
          list.push({
            id: idx,
            name: `资源名称${index}`,
            department: `所属单位${index}`,
            developor: `开发者${index}`,
            publishTime: '2021-04-21 10:' + (60 - index),
            code: 'XCSDDGDS',
            status: '状态1',
            type: 'JREGRG',
            total: index % 7,
          })
        }
        let totalRow = 24
        this.pagination = {
          // firstPage,
          // lastPage,
          pageNumber: pageNumber,
          pageSize,
          // totalPage,
          totalRow,
        }
        this.list = list
        this.loading = false
      }, 500)
    },
    onPageChange(pageNumber, pageSize) {
      this.load(pageNumber, pageSize)
    },
    onPageSizeChange(pageNumber, pageSize) {
      this.load(pageNumber, pageSize)
    },
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys
      this.selectedRows = [...selectedRows]
    },
    onDelete(record) {
      console.log(record)
    },
    handleDel() {
      let vm = this
      let selectedRowKeys = vm.selectedRowKeys
      if (selectedRowKeys.length === 0) {
        this.$message.warning('请选择至少一条记录')
        return
      }
      this.$confirm({
        title: '确认删除所选择的记录？',
        cancelText: '',
        icon: 'info-circle',
        onOk() {
          let { pageNumber, pageSize } = vm.pagination
          vm.load(pageNumber, pageSize)
        },
      })
    },
  },
  mounted() {
    let { pageNumber, pageSize } = this.pagination
    this.load(pageNumber, pageSize)
  },
}
</script>
